.tools{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height:50px;
    margin:0 auto;
}

.functionMenu input[type="radio"]{
    opacity:0;
    margin:auto 0;
    
}
.functionMenu label.mode{
    border-radius: 30px;
    background-color: white;
    padding:0.5rem 1rem;
    font-weight: bold;
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
}
.functionMenu input[type="radio"]:checked+label.mode{
    background-color: 38618C;
    color:white;
}
.icslead{
    margin:auto 10px;
}
.floorSwitch{
    background-color: white;
    position: absolute;
    bottom:0;
    left:0;
    z-index: 32;
    display: flex;
    flex-direction: column;
    font-size: 20px;
    border-radius: 0px 5px 0px 5px;
    box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
  }
  .floorSwitch input{
    display: none;
  }
  .floorSwitch label{
    padding:10px 20px;
    border-bottom: 0.5px solid rgba(0, 0, 0, 0.5);
  }
  .floorSwitch input[type="radio"]:checked+label.floor{
    color:red;
  } 
  .rightColumn{
    position: relative;
    margin-left:10px;
  }

.mapWindow{
  margin:20px auto;
  width:fit-content;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
  position: relative;
}
.buttonICS{
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  border-color:transparent;
  margin-left:5px;
}