body {
  font-family: "Acumin Pro";
  color: #021025;
  margin: 0;
  background-color:#E8E8E8;
}

p {
  font-size: 16px;
  font-weight: 400;
}

img {
  max-width: 100%;
  max-height: 100%;
}

.container {
  display: flex;
  margin-top:0px;
}

.sidebar {
  display: flex;
  flex: 1;
  height: calc(100vh - 50px);
  background-color: #fafafb;
}

.area {
  flex: 4;
}

.landscape {
  height: 300px;
  width: 800px;
}

.featuredItem {
  width: 350px;
  height: 100px;
  margin: 10px 0;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  position: relative;
}

.featuredTitle {
  font-size: 16px;
  font-weight: 600;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.featuredItem:hover {
  background-color: #e8ecef;
}
.widget{
  position: absolute;
  top:20px;
  right:20px;
  padding: 5px 10px;
}
.widgetStatusDanger {
  /* text-align: left; */
  background-color: #FFEBEB;
  color: #F7261C;
  border-radius: 5px;
  height:fit-content;
}

.widgetStatusSafe {
  /* text-align: left; */
  background-color: #e5faf2;
  color: #3bb077;
  border-radius: 5px;
  height:fit-content;
}

.info {
  font-size: 14px;
  width: 100%;
  padding: 0 10px;
}
.title {
  font-size: 16px;
  font-weight: 600;
}
.header {
  display: block;
  float: left;
}

.sidebarWrapper {
  padding: 15px;
  color: #555;
}

/* ics */
.ics{
  height: 700px;
  width: 100%;
  position: absolute;
  z-index:1;
  flex: 4 1;
  left:420px;
}

.video{
  width:40%;
  height:100%;
  background-color: #021025;
  margin:auto 0;
  display: flex;
  position: relative;
  /* border: 10px #021025 ; */
  /* border-style: solid none; */
  border-radius: 10px;
}
.liveV{
  background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url(../../assets/Elementary_classroom_in_Alaska.jpeg);
  background-size: contain;
  position: relative;
}

.novideo{
  background-image: url(../../assets/360_F_202421406_QPKY0374aJMV86MF21RCWFL84Ntm5zJL.jpeg);
  background-size: contain;

  /* border: 10px #021025 ;
  border-style: solid none; */
}


.roomInfo{
  float:left;
  margin:10px;
  margin-left:20px;
}
.video a{
  position: absolute;
  margin: auto;
  top:16%;
  left:35%;
}

.scrollbox{
  overflow: scroll;
  overflow-x: hidden;
  height:58vh;
}

::-webkit-scrollbar {
  width: 5px;

}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #d9d9d9;
  border-radius: 50px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}


.scrollboxMain{
  overflow: scroll;
  height:80vh;
}
