.video a svg{
  height:3em;
  width:3em;
  color: #E8E8E8;
  background-color: #e8e8e843;
  margin:auto;
  padding: auto;
}
.videoWindow{
  position: absolute;
  width:680px;
  z-index: 102;
  left:200px;
}
