

body {
  font-family: "Acumin Pro";
  color: #021025;
  margin: 0;
}
p {
  font-size: 16px;
  font-weight: 400;
}

.sidebar {
  display: flex;
  flex: 1;
  height: calc(100vh - 80px);
  background-color: #fafafb;
}

.featuredItem {
  width: 350px;
  height: 100px;
  border-radius: 5px;
  margin: 25px 0;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;

  display: flex;
}
.homefeaturedItem {
  width: 350px;
  border-radius: 5px;
  margin: 10px 10px;
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  position: relative;
}
.featuredTitle {
  font-size: 16px;
  font-weight: 600;
}
a:-webkit-any-link{
  text-decoration: none;
  color:#021025;
}

.sidebarListItem {
  padding: 5px;
  cursor: pointer;
  display: flex;
  align-items: center;
  border-radius: 10px;
}

.featuredItem:hover {
  background-color: #e8ecef;
}

.widgetStatus {
  text-align: left;
  background-color: #e5faf2;
  color: #3bb077;
}

.info {
  font-size: 14px;
  width: 100%;
  padding: 0 10px;
}
.title {
  font-size: 16px;
  font-weight: 600;
}
.header {
  display: block;
  float: left;
}

.homesidebarWrapper {
  color: #555;
  height:90vh;
}
.homescrollboxMain{
  overflow: scroll;
  height:90vh;
}
