

.dot{
  background-image: url(../../assets/icsp.png);
  width:2rem;
  height:2rem;
  border-radius:50%;
  position:absolute;
  background-repeat: none;
  background-size: contain;
  transform: translate(-50%, -50%);
}

.dot2{
  background-image: url(../../assets/casualty.png);
  width:1.6rem;
  height:2rem;
  border-radius:50%;
  position:absolute;
  background-repeat: none;
  background-size: contain;
  transform: translate(-50%, -50%);
}


.area1{
  background-image: url(./BgImage.png);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  border-radius: 10px;
}
.zonePanel{
    position: fixed;
    bottom:50%;
    right:0%;
    width:120px;
    background-color: white;
    z-index:2;
    display: flex;
    float:left;
    padding: 10px 0;
}
.zoneTitle{
    text-align: center;
    position: relative;
    margin:auto;

}
.panel{
    position: absolute;
    /* bottom:42%;
    right:0%; */
    top:5px;
    left:50%;
    background-color:white;
    z-index:2;
    display: flex;
    float:left;
    text-align: center;
    /* border:1px solid black; */
    padding:0.2rem 1rem;
    border-radius: 30px;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
}
.panel label{
  margin:auto;
}

input[type="radio"]{
    height:30px;
    width:30px;
    margin:5px;
    border:none;
}
.red{
  height:30px;
  width:30px;
  display: flex;
  margin:5px;
  z-index:3;
  border-radius:50%;
  accent-color: red;
}
.orange{
    height:30px;
    width:30px;
    display: flex;
    margin:5px;
    z-index:3;
    border-radius:50%;
    accent-color: orange;
}
.blue{
    height:30px;
    width:30px;
    display: flex;
    margin:5px;
    z-index:3;
    border-radius:50%;
    accent-color: green;
}
.white{
  height:30px;
  width:30px;
  display: flex;
  margin:5px;
  z-index:3;
  border-radius:50%;
  background-color: rgb(213, 213, 213);
  accent-color: white;
}
.floorplan{
  /* border:1px solid #38618c47; */
  position: relative;
  border-radius: 10px;
  background-color: white;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);

}
button{
  background-color: #FAFAFB;
  padding:10px 10px;
  border-radius: 20px;
  border-color: transparent;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.1);
}
.CCPpanel{
  position: absolute;
  /* bottom:42%;
  right:0%; */
  top:5px;
  left:30%;
  z-index:2;
  display: flex;
  float:left;
  text-align: center;
  padding:0.2rem 1rem;
  border-radius: 30px;
}
.CCPpanel button{
  margin:0 10px;
  background-color:white;
  /* border:1px solid black; */
}
svg{
  float: left;
}
.alertIcon{
  position:absolute;
  z-index: 100;
}
.videoWindow{
  position: absolute;
  width:90%;
  z-index: 102;

}