

.topbar {
  width: 100%;
  height: 60px;
  background-color: white;
  position: sticky;
  top: 0;
  z-index: 999;
  filter:invert(100%);
}
.topleft{
  height:100%;
  width: 50%;
  display: flex;
  align-content: center;
}
.topbarWrapper {
  height: 100%;
  padding: 0px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.svgContainer{
  margin:10px 0;
  width:50px;
}
.logo img{
  float:left;
  display: block;
  margin: auto;
}
.productName{
  font-size: 24px;
  font-weight: 400;
  float:left;
  margin: auto 0;
}


.topright {
  display: flex;
  align-content: center;
  text-align: center;
}

.topbarIconContainer {
  position: relative;
  text-align: center;
  margin:auto 5px;
}
